// Navigate - 렌더링 될 때 현재 위치를 변경함
//useNavigate - 특정 이벤트가 발생 했을 때 url을 조작 (이전, 다음)
import { Navigate } from "react-router-dom";

const MyPage = () => {
  const isLoggedIn = false; //기본은 로그인이 되어있지 않은 상태
  if (!isLoggedIn) {
    return <Navigate to="/login" replace={true} />;
    //만약에 값이 false라면 login페이지로 이동.
  }
  return <div>마이페이지</div>;
};

export default MyPage;
