import styled, { css } from "styled-components";

/* ``을 이용해서 만든 문자열에 스타일 정보를 넣어줌. 이런 문법을 tagged탬플릿 리터럴 이라함. */
const Box = styled.div`
  /*  해당 스타일이 적용된 div로 이루어진 리액트 컴포넌트가 생성됨.(스타일링된 엘리먼트)*/
  /* props 로 넣어준 값을 직접 전달해줄 수 있다. */
  background: ${(props) =>
    props.color || "blue"}; /* 컬러를 주지 않았을 때는 blue가 적용되도록 */
  padding: 1rem;
  display: flex;
`;

/* div가아닌 button이나 input에 스타일링을 할 수도 있다. */
const Button = styled.button`
  background: white;
  color: black;
  border-radius: 4px;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 600;

  &:hover {
    background: rgba(255, 255, 255, 0.9);
  }

  /* props에 따른 조건부 스타일링 */ /* 조건부 스타일링시 반드시 css로 묶어줘야 함. */
  ${(props) =>
    props.inverted &&
    css`
      background: none;
      border: 2px solid white;
      color: white;
      &:hover {
        background: white;
        color: black;
      }
    `};
  & + button {
    margin-left: 1rem;
  }
`;

const StyledComponent = () => {
  return (
    <Box color="black">
      <Button>안녕하세요</Button>
      <Button inverted={true}> 테두리만 </Button>
    </Box>
  );
};

export default StyledComponent;
