import { createSlice } from '@reduxjs/toolkit';

//변수로 지정하여 아래 export에 등록
let user = createSlice({
    //useState()와 비슷한 역할을 함.
    name: 'user', //state이름작명
    initialState:{name:'shin', age:20 }, //오브젝트형으로 수정
  
    //'shin'이라는 state를 수정할 함수 만들기
    reducers: {
      changeName(state) {
        //함수명 작명
        /* return {name:'인서', age:21 } */ //수정할 state내용. 오브젝트 형으로 수정
        state.name = '인서' //state의 내용을 직접 수정함.
        state.age = 21
      },
      increase(state,action){ //state변경함수를 action이라고 함. 매개변수를 action으로 설정함.
        state.age += action.payload; //state에 저장된 데이터를 파라미터로 전송함. 
        //action.payload는 해당 액션과 같이 값을 전송하기 위해 사용됨.
      }
    },
  });
  
//user함수에 필요한 요소를 모두 가져오는게 수정보완에 유리함.
export let { changeName, increase } = user.actions;

export default user; //store.js에 가져다 쓰려면 내보내기 함.