import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Nav, Navbar } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom"; //useNavigate 페이지의 이동을 도와주는 라이브러리

const Header = () => {
  let navigate = useNavigate(); //라이브러리를 실행하기 위해 변수로 지정하여 사용
  return (
    <div>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Link to="/">
            <img src="../img/logo.png" alt="cake"></img>
          </Link>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />

          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link
                onClick={() => {
                  navigate("/Company");
                }}
              >
                카페파스쿠찌
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Product");
                }}
              >
                메뉴
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Info");
                }}
              >
                매장안내
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Notice");
                }}
              >
                공지사항
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Event");
                }}
              >
                이벤트
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Cart");
                }}
              >
                장바구니
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate(1);
                }}
              >
                다음
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};

export default Header;
