import { configureStore, combineReducers } from '@reduxjs/toolkit';

import { persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';

import userSlice from './store/userSlice.js'; //분할된 user슬라이스 가져오기
import cartSlice from './store/cartSlice.js'; //분할된 cart슬라이스 가져오기

//reducer들을 결합해준다
const reducers = combineReducers({
  user: userSlice.reducer,
  cart: cartSlice.reducer,
});

const persistConfig = {
  key: 'root',
  storage: storage,
  whitelist: ['user', 'cart'],
  blacklist: ['stock'],
};

//결합한 combineReducer(reducer)을 persistReducer안에 넣어준다
const persistedReducer = persistReducer(persistConfig, reducers);

//configureStore의 reducer안에 persistReducer를 넣어준다.
// 변경 후
const store = configureStore({
  reducer: persistedReducer,
});

export default store;