import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import { useSelector, useDispatch } from 'react-redux'; //redux가져오기
import { changeName, increase } from './../store/userSlice'; //변경된 state함수 가져오기
import { addCount, delCount, delItem } from '../store/cartSlice';
import { Link } from 'react-router-dom';


function Cart() {
  //redux store가져오는 함수 아래 코드 입력하고 리액트 재시작
  //아래 코드 입력하면 redux store에 있던 state를 가져올 수 있음.
  //변수 선언하여 원하는 곳에 바인딩 하여 사용
  let state = useSelector((state) => {
    return state;
  });
  let dispatch = useDispatch(); //useDispatch()는 store로 요청 보내주는 함수

  //아이템 총가격
  /* let totalPrice = state.cart[0].price * state.cart[0].count;
  let totalPrice2 = state.cart[1].price * state.cart[1].count; */

  let totalPrice = [0]; //가격초기화
  let i = 0; //상품을 증가시킬 인덱스 초깃값

  for (i = 0; i < state.cart.length; i++) {
    let totalAdd = state.cart[i].price * state.cart[i].count; //해당 인덱스들의 가격과 갯수를 곱하여 변수로 만듬
    totalPrice.push(totalAdd);
  }
  // const sum = totalPrice.reduce((accumulator, currentValue) => accumulator + currentValue);
  const sum = totalPrice.reduce((x, y) => x + y);
  // reduce()배열에 있는 모든 요소의 합계를 반환

  /*  (accumulator, currentValue) => accumulator + currentValue,
      (accmulator는 콜백의 반환값을 누적  + 현재의 값) 
 */

  return (
    <Container>

      <Row className="mt-5">
        <Col className="mt-5">
          {state.user.name} {state.user.age}의 장바구니{/* 변경될 기존 state */}
          <button onClick={() => { //2.버튼태그에 클릭이벤트 적용
            dispatch(increase(10))//직접 입력한 값이 동적으로 변환됨.
          }}>
            버튼
          </button>

          <Table striped bordered hover>
            <thead>
              <tr>
                <th>No</th>
                <th>이미지</th>
                <th>상품명</th>
                <th>수량</th>
                <th>가격</th>
                <th>변경하기</th>
              </tr>
            </thead>
            <tbody>
              {/* map반복문을 사용하여 state에 보관된 데이터들이 자동으로 늘어나도록 함. */}
              {state.cart.map((a, i) => (
                <tr key={i}>
                  <td>{state.cart[i].id}</td>
                  <td><img src={state.cart[i].img} alt="listImg" /></td>
                  <td>{state.cart[i].name}</td>
                  {/* <td>{state.cart[i].count}</td> */}
                  <td>
                    <button
                      onClick={() => {
                        /* state변경함수, 해당버튼의 id를 찾아 수량에 -1 */
                        dispatch(delCount(state.cart[i].id));
                      }}
                    >
                      -
                    </button>
                    {state.cart[i].count} {/* 카운트 */}
                    <button
                      onClick={() => {
                        /* state변경함수, 해당버튼의 id를 찾아 수량에 +1 */
                        dispatch(addCount(state.cart[i].id));
                      }}
                    >
                      +
                    </button>
                  </td>
                  <td>
                    {state.cart[i].price * state.cart[i].count}
                  </td>
                  <td
                    onClick={() => {
                      dispatch(delItem(state.cart[i].id));
                    }}
                  >
                    삭제
                  </td>
                </tr>
              ))}
              <tr>
                <td colspan="6">
                  총금액 : {sum}원
                  <Link to="/Login">
                    <button>결제하기</button>
                  </Link>
                </td>
              </tr>
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
}

export default Cart;
