import { Link } from "react-router-dom";
const Product = () => {
  return (
    <div>
      <ul>
        <li>
          <Link to="/Company">회사소개</Link>
        </li>
        <li>
          <Link to="/Product">제품소개</Link>
        </li>
      </ul>
      <p>제품소개 페이지</p>
    </div>
  );
};

export default Product;
