import { createSlice } from '@reduxjs/toolkit';

//store.js의cart그대로 가져옴.
let cartSlice = createSlice({
  name: 'cart',
  initialState: [],
  reducers: {
    //state를 수정할 함수를 reducer안에 작성
    addCount(state, action) {
      //state변경함수 action을 파라미터로 설정.
      // state[action.payload].count++

      let num = state.findIndex((a) => {
        //a는 state안의 array자료들~ id가 같은 상품을 찾아
        return a.id === action.payload; //비교하여 같다면 값을 반환
      });
      state[num].count++;
    },
    addItem(state, action) {
     // state.push(action.payload); //push()는 배열 요소의 마지막에 자료 추가해 주는 함수
     let num = state.findIndex((obj) =>{
        return obj.id === action.payload.id;
     });
     if(num === -1){
        state.push(action.payload);
     }else{
        state[num].count += action.payload.count;
     }
    },
    delCount(state, action) {
      // state[action.payload].count--
      let num = state.findIndex((a) => {
        //array객체중 id가 같은 상품을 찾아
        return a.id === action.payload; //비교하여 같다면 값을 반환
      });
      if (state[num].count > 1) state[num].count--; //1이하로 감소하지 않도록함.
    },
    delItem(state, action) {
      let del = state.filter((a) => {
        return a.id !== action.payload;
      });
      return del;
    },
  },
});

//cart함수에 필요한 요소를 모두 가져오는게 수정보완에 유리함.
export let { addCount, addItem, delCount, delItem } = cartSlice.actions; //addCount를 변수로 만들어 내보내기

export default cartSlice; //store.js에 가져다 쓰려면 export로 내보내기 함.